@font-face {
  font-weight: normal;
  font-family: 'seti';
  font-style: normal;
  src: url('../../../../assets/static/fonts/seti/seti.eot');
  src: url('../../../../assets/static/fonts/seti/seti.eot?#iefix') format('eot'),
    url('../../../../assets/static/fonts/seti/seti.woff2') format('woff2'),
    url('../../../../assets/static/fonts/seti/seti.woff') format('woff'),
    url('../../../../assets/static/fonts/seti/seti.ttf') format('truetype'),
    url('../../../../assets/static/fonts/seti/seti.svg#seti') format('svg');
}

.icon-base-pseudo {
