.icon:before {
  content: '\E021';
  &:extend(.icon-base-pseudo);
}

.icon(@filename, @color: @white, @insert: before) {
  @pseudo-selector: ~':@{insert}';

  &@{pseudo-selector} {
    .icon-char(@filename);
    color: @color;
  }
}

