:global {
  .drag-image {
    position: absolute;
    top: -200px;
    left: -200px;
    display: inline-block;
    padding: 1px 7px;
    overflow: hidden;
    color: rgb(31, 30, 30);
    font-size: 13px;
    background-color: #ffffffde;
    border-radius: 10px;
  }
  .explore-panel-header {
    text-transform: none !important;
  }
}

.explorer {
  width: 100%;
  height: 100%;
  :global {
    .operate {
      white-space: nowrap;
      & > i {
        margin-right: 16px;
        font-size: 16px;
        vertical-align: middle;
      }
    }
  }
}
