.htmlPreview {
  width: 100%;
  height: 100%;
  .header {
    z-index: 2;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    height: 35px;
    min-height: 35px;
    padding: 0.25rem 0.25rem 0.25rem 0.5rem;
    line-height: 1;
    -webkit-box-align: center;
  }
  iframe {
    z-index: 1;
    background-color: white;
    border: 0;
    opacity: 1;
    pointer-events: initial;
  }
}
