@primary-console-font-color: #e0e0e0;

.console {
  position: relative;
  width: 100%;
  height: 100%;

  .actionMenu {
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    width: 30px;
    min-width: 30px;
    background-color: #333333;

    button {
      color: @primary-console-font-color;
    }

    .icon {
      padding: 6px 8px 8px 8px;

      img,
      svg {
        width: 12px;
        height: 12px;
      }

      &:hover {
        background-color: rgba(64, 169, 243, 0.1);
        cursor: pointer;
      }
    }
  }

  .terminal {
    width: 100%;
    padding: 10px;
    text-align: left;

    .waiting {
      display: block;
      margin-top: 150px;
      color: #fff;
      font-size: 2.3em;
      text-align: center;
    }

    .loading {
      display: block;
      height: 550px;
      padding-top: 30px;
      overflow: hidden;
      color: #fff;
      font-size: 2.3em;
      text-align: center;
    }
  }

  .breakWord {
    word-wrap: break-word;
    word-break: break-all;
  }

  .std1 {
    color: @primary-console-font-color;
  }

  .std2 {
    color: #cf0002;
  }

  .std3 {
    color: #fab563;
  }

  .std4 {
    color: #59a600;
  }
}

.externalProviderWarning {
  background-color: #700000;

  & > h2 {
    color: #fef7ff;
  }

  & > div {
    color: #fef7ff;
  }
}
