.container {
  position: fixed;
  top: 35px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99;
  width: 400px;
  background-color: #292929;
}

.commandInputWrap {
  width: 100%;
  padding: 6px;
}

.commandInput {
  border: 1px solid #1b1c1e;
}

.commandList {
  width: 100%;
  box-sizing: border-box;
  border-top: none;
}

.command {
  width: 100%;
  cursor: pointer;
  padding: 5px 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;

  &:hover {
    background-color: #106ebe;
  }
}
