.control {
  width: 28px;
  margin-top: auto;
  margin-bottom: auto;
  line-height: 35px;
  text-align: right;
  visibility: hidden;
  & > i {
    margin-right: 0.5em;
    vertical-align: middle;
  }
}

.unSaved {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #bcbcbc;
  display: inline-block;
  margin-right: 0.5em;
  vertical-align: middle;
}
