:global {
  .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background-color: #1b92cc70;
    opacity: 0.3;
    pointer-events: auto;
  }
  .file-split {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
  }
}

.centerPane {
  width: 100%;
  height: 100%;

  .notInteractive {
    height: 100%;
    :global {
      iframe {
        pointer-events: none !important;
      }
    }
  }

  .isInteractive {
    height: 100%;
    :global {
      iframe {
        pointer-events: auto;
      }
    }
  }

  & > .empty {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    img {
      width: 80%;
      max-width: 325px;
    }
  }

  :global {
    div.item::before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      z-index: 99;
      display: block;
      width: 2px;
      background-color: #393a3b;
      content: ' ';
    }
    div.item:first-child::before {
      display: none;
    }
  }
}
