.headerMenu {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  .menu {
    display: flex;
    user-select: none;
    & > div {
      margin-left: 1px;
      border-radius: 2px;
      display: inline-block;
      vertical-align: middle;
      padding: 4px 12px;
      cursor: pointer;
      font-weight: 600;
      transition: background-color 0.3s;
      &:hover {
        background-color: #3b3d3f;
      }
    }
  }
}
