.compose {
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;
  .header {
    display: flex;
    flex-shrink: 0;
    box-sizing: border-box;
    height: 35px;
    padding-right: 4px;
    padding-left: 4px;
    overflow: hidden;
    box-shadow: 0px 4px 8px -4px rgba(0, 0, 0, 0.5);
    user-select: none;
    .label {
      padding-left: 12px;
      overflow: hidden;
      line-height: 35px;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .extra {
      flex: 1;
      padding-left: 5px;
    }
  }
  .content {
    flex: 1;
    padding: 12px 0 0 0;
    overflow: hidden;
  }
}
