.tabsContainer {
  display: flex;
  .tabs {
    display: flex;
    flex: 1;
    flex-shrink: 0;
    overflow-x: auto;
    overflow-y: hidden;
    &::-webkit-scrollbar {
      width: 5px;
      height: 5px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba(121, 121, 121, 0.4);
    }
    &::-webkit-scrollbar-track {
      background-color: #1a1c1e;
    }
    .left {
      flex: 1 0 auto;
      position: relative;
    }
    .tab {
      position: relative;
      display: flex;
      flex-shrink: 0;
      box-sizing: border-box;
      min-width: 120px;
      min-width: fit-content;
      height: 35px;
      padding-left: 10px;
      white-space: nowrap;
      border-right-width: 1px;
      border-right-style: solid;
      cursor: pointer;
      &.context {
        outline-width: 1px;
        outline-style: solid;
        outline-offset: -1px;
      }
      .label {
        min-width: 0;
        max-width: 150px;
        margin-top: auto;
        margin-bottom: auto;
        overflow: hidden;
        line-height: 35px;
        text-overflow: ellipsis;
      }
      .status {
        line-height: 35px;
      }
      &.active {
        left: auto;
        .control {
          visibility: visible;
        }
      }
      &:hover {
        .control {
          visibility: visible;
        }
      }
      .unSaved {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #bcbcbc;
        display: inline-block;
        margin-right: 0.5em;
        vertical-align: middle;
      }
    }
    :global {
      .icon:before {
        font-size: 22px;
        line-height: 35px;
      }
    }
  }
  .actions {
    height: 35px;
    padding: 0 8px 0 4px;
    font-size: 16px;
    line-height: 35px;
    text-align: right;
    cursor: default;
    & > i {
      padding: 0 7px;
      cursor: pointer;
    }
  }
}
