.container {
  margin: 5px 0;
  padding: 5px 10px;
  background-color: rgb(27, 28, 30);
  border-radius: 2px;

  .item {
    display: flex;
    .icon_wrapper {
      flex-basis: 36px;
      width: 36px;
      display: flex;
      align-items: center;
      i {
        color: #fff;
      }
      .icon {
        display: block;
        width: 28px;
        height: auto;
        max-height: 28px;
        font-size: 25px;
        line-height: 100%;
        text-align: center;
      }
    }
    .content_wrapper {
      display: flex;
      justify-content: center;
      flex-direction: column;
    }
    .title {
      color: #f5f4f2;
      font-size: 14px;
    }
    .desc {
      font-size: 12px;
      color: #edebe9;
    }
    .extra {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }
}
