.breadcrumbs {
  .relativePath {
    display: flex;
    flex: 1 100%;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-start;
    height: 22px;
    padding-left: 16px;
    outline-style: none;
    cursor: default;
    user-select: none;
    i {
      margin: 0 4px 0 8px;
    }
  }
  .breadcrumbItem {
    &::before {
      margin-right: 8px;
      font-size: 22px;
      vertical-align: middle;
    }
    .labelContent {
      display: inline-block;
      max-width: 150px;
      overflow: hidden;
      overflow: hidden;
      line-height: 22px;
      white-space: nowrap;
      text-overflow: ellipsis;
      vertical-align: middle;
    }
  }
}
