.compose {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  .header {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    margin: 0 -5px;
    height: 35px;
    padding-right: 15px;
    padding-left: 15px;
    overflow: hidden;
    user-select: none;
    .tabPane {
      height: 100%;
      margin-right: 15px;
      font-weight: 400;
      line-height: 35px;
      cursor: pointer;
    }
    .operation {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    .extra {
      display: flex;
      align-items: center;
    }
  }
  .content {
    width: 100%;
    height: calc(~'100% - 35px');
  }
}
